<template>
    <!-- 这一块无axios的辅助功能 -- 麻烦QAQ ===> 上传图片功能，在项目中经常用到，这种应该封装成一个组件 -->
    <!--  action="/upload/common/" ==> 后台接口地址 -- 上传地址 -->
    <!-- action 地址要完整的后台地址 http://localhost:3001/upload/common/ -->
    <!-- 但此处是 经过elementUI 封装的图片上传组件 -- 未经过axios -- 所以不涉及请求拦截器 -- 无header头 -- 所以要设置header头 -- 看elementUI文档 -->
    <!-- headers设置在data()中，简化一下 -->
    <!-- v-bind指令（即:）对于数据类型的绑定特别好用 -->
    <el-upload :on-success="handleUploadSuccess" :on-error="handleUploadError" :before-upload="beforeAvatarUpload"
        accept="image/*" :show-file-list="false" :data="data" :action="action" :headers="headers" class="avatar-uploader">
        <img v-if="picture" :src="picture" class="avatar">
        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
    </el-upload>
</template>

<script>
export default {
    // model ==> 对象数组形式，多个事件?
    model: {
        prop: 'picture',
        event: 'change'
    },
    props: {
        data: {
            type: Object,
            default: () => {
                return { type: 'common' };
            }
        },
        headers: {
            type: Object,
            default: () => {
                return { Authorization: `Bearer ${sessionStorage.access_token}` };
            }
        },
        action: {
            type: String,
            default: `${process.env.VUE_APP_SERVER}/upload/common/`,
        },
        picture: {
            type: String,
            default: '',
        }
    },
    data() {
        return {

        }
    },
    methods: {
        // 文件上传前的钩子
        // element 框架文档 -- 长传头像前的操作 -- 一般就是判断一遍图片是否合规
        beforeAvatarUpload(file) {
            // 打印 file 一下 ==> 显示file是上传的图片对象，包含图片的各种信息。 

            // 此处使用一个正则限制图片格式
            let reg = /^image\/(jpeg||png)$/;
            const isJPG = reg.test(file.type);
            // 限制图片大小 小于两兆
            const isLt2M = file.size / 1024 / 1024 < 2;

            if (!isJPG) {
                this.$message.error('上传头像图片只能是 JPG 格式!');
            }
            if (!isLt2M) {
                this.$message.error('上传头像图片大小不能超过 2MB!');
            }
            // 两个限制条件都合规，才会返回 true
            return isJPG && isLt2M;
        },
        // 文件上传失败的时的钩子
        handleUploadError(error, file, fileList) {
            // console.dir(error)
            // 添加了 状态码 报错信息提示
            // 此处 应用了 解构起别名
            let { status, message } = error;
            switch (status) {
                case 400:
                    // 因为 error.message 是混杂了其他字符的字符串，但我们只想要其中的提示信息。
                    // 所以 用 JSON.parse 把 error.message 转化为 对象去使用。
                    let { status, msg } = JSON.parse(message);
                    // 信息提示
                    this.$message.error(msg);
                    break;
                case 401:
                    let { code } = JSON.parse(message);
                    this.$message.error(`错误代码401,${code},请重新登录`);
                    break;
                default:
                    break;
            }
        },
        // 文件上传成功时的钩子
        handleUploadSuccess(response, file, fileList) {
            let { status, msg, src } = response;
            if (status) {
                // 预览图片
                this.$emit('change', src);
                // 信息提示
                this.$message.success(msg);
            }
        },
    },
}
</script>

<style lang="less" scoped>
.avatar-uploader .el-upload {
    border: 2px solid #111010;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
}

.avatar-uploader .el-upload:hover {
    border-color: #409EFF;
}

.avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
}

.avatar {
    width: 178px;
    height: 178px;
    display: block;
}
</style>