<template>
    <el-card class="box-card">
        <div slot="header">
            <span>编辑文章</span>
        </div>
        <el-form :model="form" :rules="rules" ref="form" label-width="100px">
            <el-form-item label="标题" prop="title">
                <el-input type="text" v-model="form.title" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="描述" prop="description">
                <el-input type="text" v-model="form.description" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="分类">
                <el-col :span="6">
                    <el-select v-model="form.cate_1st" @change="handleCategoryChange" placeholder="请选择一级分类">
                        <el-option v-for="item in cate_1st_options" :label="item.cate_name" :value="item.cate_id"
                            :key="item.cate_id"></el-option>
                    </el-select>
                </el-col>
                <el-col :span="6">
                    <el-select v-model="form.cate_2nd" placeholder="请选择二级分类">
                        <el-option v-for="item in cate_2nd_options" :label="item.cate_name" :value="item.cate_id"
                            :key="item.cate_id"></el-option>
                    </el-select>
                </el-col>
            </el-form-item>
            <el-form-item label="图片" prop="main_photo">
                <ImgUpload v-model="form.main_photo"></ImgUpload>
            </el-form-item>
            <el-form-item label="内容" prop="content">
                <txtEditor v-model="form.content" :content="form.content"></txtEditor>
            </el-form-item>
            <el-form-item>
                <el-button @click="handleEdit" type="primary">发布文章</el-button>
            </el-form-item>
        </el-form>
    </el-card>
</template>

<script>
import { Article, Category } from '@/api';

// 引入图片上传组件
import ImgUpload from '@/components/Img-upload.vue';
// 引入富文本编辑器组件
import txtEditor from '@/components/txt-editor.vue';

export default {
    name: 'Edit',
    components: {
        ImgUpload,
        txtEditor,
    },
    data() {
        return {
            form: {
                cate_1st: '',
                cate_2nd: '',
                title: '',
                description: '',
                main_photo: '',
                content: '',
            },
            // 分类列表
            cate_1st_options: [],
            cate_2nd_options: [],
            // 表单验证规则
            rules: {
                main_photo: [
                    { required: true, message: '请上传图片！', trigger: 'blur' },
                ],
                title: [
                    { required: true, message: '请输入文章标题！', trigger: 'blur' },
                ],
                description: [
                    { required: true, message: '请输入文章描述！', trigger: 'blur' },
                ],
                content: [
                    { required: true, message: '请输入文章内容！', trigger: 'blur' },
                ],
            },
        }
    },
    async created() {
        let { article_id } = this.$route.params;
        this.init(article_id);
    },
    methods: {
        // 初始化页面
        async init(article_id) {
            // 一级列表
            let { status: category_status, data: category_data } = await Category.list();
            if (category_status) {
                this.cate_1st_options = { ...category_data };
            }
            // 加载资料
            let { status: article_status, data: article_data } = await Article.detail({ article_id });
            if (article_status) {
                // 还原表单
                this.form = { ...article_data };
            }
            let { cate_1st } = article_data;
            // 加载二级列表
            let { status: subcate_status, data: subcate_data } = await Category.subCate({ cate_id: cate_1st });
            if (subcate_status) {
                this.cate_2nd_options = { ...subcate_data };
            }
        },
        // 监控一级分类的选择 -- 请求二级二级分类  ==> @change事件
        async handleCategoryChange(cate_id) {
            // 加载二级列表
            let { status, data } = await Category.subCate({ cate_id });
            if (status) {
                this.cate_2nd_options = { ...data };
            }
            // 二级列表默认选第一项
            this.form.cate_2nd = this.cate_2nd_options[0]?.cate_id;
        },
        // 保存编辑文章
        async handleEdit() {
            // 表单验证的第二种写法 ==> 参考 Element UI ==> validate中不传入参数(即回调函数)时
            // 则会返回一个 promise ==> 可使用 try catch 捕获成功与失败 
            try {
                let valid = await this.$refs.form.validate();
                if (valid) {
                    let { status, msg } = await Article.edit({ ...this.form });
                    if (status) {
                        // 信息提示
                        this.$message.success(msg);
                    } else {
                        this.$message.error(msg);
                    }
                }
            } catch (error) {
                this.$message.error(error);
            }
        },

    },
}
</script>

<style lang="less" scoped></style>
